import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../@uisftech/common/constant/config';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
/**
 * 页面维护详情
 */
@Component({
    selector: 'page-gather-maintain-detail',
    templateUrl: './page-gather-maintain-detail.component.html',
    styleUrls: ['./page-gather-maintain-detail.component.scss'],
    providers: [InterfaceHttpService]
})

export class pageGatherMaintainDetailComponent implements OnInit {
    @Input('in-value') inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    tableData: any = [];
    msgs: any;
    isGather: boolean = false;
    tableData1: any;
    code: any;
    pageShowStyleOptions: any = [];
    constructor(
        public interfaceHttpService: InterfaceHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        this.codeValues();
        this.pageShowStyleOptions = this.code['PageShowStyle'];//排放形式
        this.query({ pageCollectId: this.inValue });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    query(param) {
        this.interfaceHttpService.queryPageCollectDefinitionDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageShowStyle && data.pageShowStyle != '') {
                    for (let item of this.pageShowStyleOptions) {
                        if (item.value == data.pageShowStyle) {
                            data.pageShowStyle = item.label;
                        }
                    }
                }
                this.tableData = data
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //返回
    return() {
        this.outValue.emit(false)
    }

    //详情
    deDetail(col) {
        this.isGather = true;
        let param = {
            pageId: col.pageId,
            pageCollectId: this.tableData.pageCollectId
        }
        this.queryGather(param)
    }

    //查询规则详情
    queryGather(param) {
        this.interfaceHttpService.queryPageRulesControllerInfoById(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData1 = data
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //返回
    return1() {
        this.isGather = false;
    }
}