import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../../@uisftech/common/constant/config';
import { InterfaceHttpService } from '../../../../http/interface.http.servive';
import { FileCenterHttpService } from '../../../../../../custom/file-center/http/file-center.http.service';
import { RuleInfoBean } from '../../../../../../custom/file-center/bean/rule-info.bean';
/**
 * 页面规则设置
 */
@Component({
    selector: 'rule-set',
    templateUrl: './rule-set.component.html',
    styleUrls: ['./rule-set.component.scss'],
    providers: [InterfaceHttpService,FileCenterHttpService]
})

export class RuleSetComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inValue: any;
    tableList:any=[];
    msgs:any[];
    first: number = 0;//首页
    total: any; //总页数
    detailDisplay:boolean = false;
    arr:any = [];
    list:any;
    checkValue:any = [];
    ruleInfoBean:RuleInfoBean = new RuleInfoBean()
    constructor(
        public interfaceHttpService:InterfaceHttpService,
        public fileCenterHttpService:FileCenterHttpService
    ) {

    }

    ngOnInit() {
        this.query()
    }

    query(){
        this.interfaceHttpService.queryPageRulesControllerInfoById(this.inValue).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
    //新增按钮
    addieldTemplate() {
        this.checkValue = []
        if(!this.tableList.drList){
            this.tableList.drList = []
        }
        this.arr = this.tableList.drList;
        if(this.tableList.drList&&this.tableList.drList.length>0){
            this.tableList.drList.forEach(element => {
                this.checkValue.push(element.drId)
            });
        }
        this.detailDisplay = true;
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.queryClick()
    }
    //删除
    deleteTemplate(i) {
        this.tableList.splice(i, 1);
        this.tableList = _.clone(this.tableList);
    }

    queryClick() {
        this.fileCenterHttpService.findDocumentRules(this.ruleInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.rulesList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    save() {
        if((!this.tableList.drList)||(this.tableList.drList.length == '0')){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请配置页面规则" });
            return
        }
        let arr = [];
        this.tableList.drList.forEach(element => {
            let obj = {}
            obj['dr_id'] = element.drId;
            arr.push(obj)
        });
        let param = {
            pageCollectId:this.tableList.pageCollectId,
            pageRulesList:{
                page_Id:this.tableList.pageId,
                rulesList:arr
            }
        }
        this.interfaceHttpService.dualPageRulesController(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false)
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '配置成功' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //重置
    reset() {
       this.tableList.drList = [];
       this.tableList.drList = _.clone(this.tableList.drList)
    }

    //取消
    cancel(){
        this.outValue.emit(false)
    }

     //分页
     paginate(event) {
        //每页显示的条数
        this.ruleInfoBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.ruleInfoBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.ruleInfoBean.pageSize;
        this.queryClick();//调用查询的方法
    }

    select(event, item) {
        if (event == true) {
            this.arr.push(item)
        }
        if (event == false) {
            this.arr.forEach((element, index) => {
                if (element.drId == item.drId) {
                    this.arr.splice(index, 1)
                }
            });
        }
    }

    sure(){
        this.detailDisplay = false;
        if(!this.tableList.drList){
            this.tableList.drList = []
        }
        this.tableList.drList = _.clone(this.arr)
    }

    //删除
    deleteField(i) {
        this.tableList.drList.splice(i, 1);
        this.tableList.drList = _.clone(this.tableList.drList);
    }



}