import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
import { PageMaintainBean } from '../../../page-preserve/page-maintain/bean/page-maintain.bean';
/**
 * 页面集配置
 */
@Component({
    selector: 'page-gather-definiton',
    templateUrl: './page-gather-definiton.component.html',
    styleUrls: ['./page-gather-definiton.component.scss'],
    providers: [InterfaceHttpService]
})

export class PageDefinitonComponent implements OnInit {
    tableData:any=[];
    msgs:any;
    pageCollectId:string;
    detailDisplay:boolean = false;
    ruleSet:any;
    isRule:boolean = false;
    //分页
    total: number;
    first: number = 0;
    arr:any=[];
    checkValue:any = [];
    pageList:any;
    pageMaintainBean:PageMaintainBean = new PageMaintainBean()
    constructor(
        public interfaceHttpService:InterfaceHttpService,
        public activatedRouter: ActivatedRoute,
        public router: Router,
    ) {
        
    }

    ngOnInit() {
        this.pageMaintainBean.pageNum = 10;
        this.pageMaintainBean.pageSize = 1;
        this.activatedRouter.queryParams.subscribe(params => {
            this.pageCollectId = params.pageCollectId;
          })
        this.query({pageCollectId:this.pageCollectId});
    }

    query(param) {
        this.interfaceHttpService.queryPageCollectDefinitionDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //查询
    queryClick() {
        this.interfaceHttpService.queryPageDefinitionList(this.pageMaintainBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.pageList = data.pageList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    pageSet() {
        debugger
        this.checkValue = []
        if(!this.tableData.pageList){
            this.tableData.pageList = []
        }
        this.arr = this.tableData.pageList;
        if(this.tableData.pageList&&this.tableData.pageList.length>0){
            this.tableData.pageList.forEach(element => {
                this.checkValue.push(element.pageId)
            });
        } 
        this.isRule = true;
        this.pageMaintainBean.pageNum = 1;
        this.pageMaintainBean.pageSize = 10;
        this.queryClick()
    }

    deSet(item){
        this.detailDisplay = true;
        this.ruleSet = {
            pageCollectId:this.pageCollectId,
            pageId:item.pageId
        }
    }

    select(event, item) {
        if (event == true) {
            this.arr.push(item)
        }
        if (event == false) {
            this.arr.forEach((element, index) => {
                if (element.pageId == item.pageId) {
                    this.arr.splice(index, 1)
                }
            });
        }
    }

     //删除
     deleteField(i) {
        this.tableData.pageList.splice(i, 1);
        this.tableData.pageList = _.clone(this.tableData.pageList);
    }

    rulesetCall(event) {
        this.detailDisplay = false;
    }

    sure() {
        this.isRule = false;
        if(!this.tableData.pageList){
            this.tableData.pageList = []
        }
        this.tableData.pageList = _.clone(this.arr)
    }

    save() {
        if(this.tableData.pageList&&this.tableData.pageList.length<1){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请配置页面" })
            return
        }
        let arr = [];
        this.tableData.pageList.forEach(element => {
            let obj = {}
            obj['page_Id'] = element.pageId;
            arr.push(obj)
        });
        let param = {
            pageCollectId:this.tableData.pageCollectId,
            pageRulesList:arr
        }
        this.interfaceHttpService.dualPageRulesController(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "保存成功" })
                this.router.navigate(['/pages/tzb/page-layout/page-data-set/page-gather-maintain'])
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    reset(){
        this.tableData.pageList = [];
        this.tableData.pageList = _.clone(this.tableData.pageList)
    }

    cancel(){
        this.router.navigate(['/pages/tzb/page-layout/page-data-set/page-gather-maintain'])
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.pageMaintainBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.pageMaintainBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageMaintainBean.pageSize;
        this.queryClick();//调用查询的方法
    }
}