import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { PageGatherMaintainAddBean } from '../bean/PageGatherMaintain.bean';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
import { checkEnAndNum } from 'app/@uisftech/common/service/validator';
/**
 * 页面集维护新增
 */
@Component({
    selector: 'page-gather-maintain-add',
    templateUrl: './page-gather-maintain-add.component.html',
    styleUrls: ['./page-gather-maintain-add.component.scss'],
})

export class PageGatherMaintainAddComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    pageGatherMaintainAddBean: PageGatherMaintainAddBean = new PageGatherMaintainAddBean()
    msgs: Message[];

    code: any;
    pageShowStyleOptions: any = [];
    constructor(
        public interfaceHttpService: InterfaceHttpService,
        public commfunc: Commfunc,
    ) {

    }

    ngOnInit() {
        this.codeValues();
        this.pageShowStyleOptions = this.code['PageShowStyle'];//排放形式
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //保存
    save() {
        for (let item of this.inValue) {
            if (item.pageCollectId == this.pageGatherMaintainAddBean.pageCollectId) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '页面集ID重复!' }];
                return
            }
        }
        if (!this.pageGatherMaintainAddBean.pageCollectId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入页面集ID' });
            return
        }
        if (!this.pageGatherMaintainAddBean.pageCollectName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入页面集名称' });
            return
        }
        if (!this.pageGatherMaintainAddBean.pageShowStyle) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择页面排放形式' });
            return
        }
        this.interfaceHttpService.createPageCollectDefinition(this.pageGatherMaintainAddBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' })
                this.outValue.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //重置
    reset() {
        this.pageGatherMaintainAddBean = new PageGatherMaintainAddBean();
    }

    //取消
    cancel() {
        this.outValue.emit(false)
    }
    //校验只能输入数字字母
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkEnAndNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                event.target.value = '';
                return
            }
        }
    }
}