import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { PageGatherMaintainBean } from './bean/PageGatherMaintain.bean';
import { InterfaceHttpService } from '../../http/interface.http.servive';
/**
 * 页面集维护界面
 */
@Component({
    selector: 'page-gather-maintain',
    templateUrl: './page-gather-maintain.component.html',
    styleUrls: ['./page-gather-maintain.component.scss'],
    providers: [ConfirmationService]
})
/**
 * 页面集配置详情
 */
export class PageGatherMaintainComponent implements OnInit {
    tableData: any = [];
    updateDisplay: boolean = false;
    detailDisplay: boolean = false;
    elementDisplay: boolean = false;
    showFlag: string;
    headerTitle: string;
    pageGatherMaintainBean: PageGatherMaintainBean = new PageGatherMaintainBean()
    //分页
    total: number;
    first: number = 0;
    msgs: Message[];
    inValue: string;
    updateValue: any;
    inValueAdd: any;//新增页面集传值
    code: any;
    pageShowStyleOptions: any = [];
    constructor(
        public confirmationService: ConfirmationService,
        public router: Router,
        public interfaceHttpService: InterfaceHttpService,
        public commfunc: Commfunc,

    ) {
    }

    ngOnInit() {
        this.codeValues();
        this.pageShowStyleOptions = this.code['PageShowStyle'];//排放形式

        this.pageGatherMaintainBean.pageNum = 1;
        this.pageGatherMaintainBean.pageSize = 10;
        this.queryClick();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //新增
    add() {
        this.inValueAdd = this.tableData;
        this.showFlag = '1';
        this.headerTitle = '新增';
        this.updateDisplay = true;

    }

    //新增回调
    addCall(event) {
        this.updateDisplay = false;
        this.queryClick()
    }

    //查询
    queryClick() {
        this.interfaceHttpService.queryPageCollectDefinitionList(this.pageGatherMaintainBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.pageCollectList;
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //重置
    reClean() {
        this.pageGatherMaintainBean.pageCollectName = '';
    }

    //删除
    detele(item) {
        this.confirmationService.confirm({
            message: '确定要删除吗?',
            accept: () => {

            }
        });
    }

    //页面定义
    deMaintain(item) {
        this.router.navigate(['/pages/tzb/page-layout/page-data-set/page-gather-definition'], { queryParams: { pageCollectId: item.pageCollectId } })
    }

    //修改
    deupdate(param) {
        this.showFlag = '2';
        this.headerTitle = '修改';
        this.updateDisplay = true;
        this.updateValue = param;
    }

    //修改回调 
    updateCall(event) {
        this.updateDisplay = false;
        this.queryClick()
    }

    //详情
    deDetail(param) {
        this.detailDisplay = true;
        this.inValue = param.pageCollectId;
    }

    //详情回掉
    detailCall(event) {
        this.detailDisplay = false;
    }

    //分页
    paginate(event) {
        this.pageGatherMaintainBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageGatherMaintainBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }

}